<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="3"
            v-if="currentStep === steps.findIndex(step=>step.key === 'getReason')"
            :handle-business="getReason"
        />
        <reason v-if="currentStep === steps.findIndex(step=>step.key === 'reason')" :reasons="reasons" :nextFunction="handleReason"/>

        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="saveFace"
        />
		<high-photo v-if="currentStep === steps.findIndex(step=>step.key === 'highPhoto')" itemName="介绍信" :nextFunction="highPhotoNext"/>
		<person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>

		<process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'print')"
            :handle-business="handlePrint"
            wait-text="正在打印中，请稍等"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            page-title="办理完成"
            :data="result"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import personInfo from './components/person-info';
import highPhoto from '@/view/pageComponents/high-photo';
import reason from './components/reason';
import process from '@/components/process';
import complete from '@/components/complete';
import { saveFaceReco, proveComplete, getNoCrimeReasons, uploadLetter } from '@/service/human/prove';
import { toPrint } from '@/service/common';
import { getPrintPrefix } from '@/utils';
import { addPrefix } from '@/utils/tools';
import { NO_CRIME_REASON_YINCHUAN } from '@/constant/constant';
import { NO_CRIME_BENGBU, NO_CRIME_YINCHUAN } from '@/constant/stepsOption';
import { checkNoCrimeBusiness } from '@/utils/regional';

export default {
    name: 'NoCrimeYinchuanIndex', // 无违法犯罪证明
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        personInfo,
        process,
        complete,
        reason,
        highPhoto
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera',
            printId: 'printId',
            businessTypeCode: 'businessTypeCode'
        })
    },
    data () {
        return {
            result: {}, // 无违法犯罪证明完成返回结果
            pdfUrl: '',
            reasons: NO_CRIME_REASON_YINCHUAN,
            useType: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'setSteps', 'changeTipPage', 'setPrintImgUrls']),
        async saveFace () {
            const res = await saveFaceReco({
                businessType: 'wfzzm', // 业务类型 wfzzm 无违法犯罪证明
                idNum: this.identifyCard.certNumber,
                identifiedPhoto: this.binocularCamera,
                useType: this.useType
            });
            if (res && res.code === '0') {
                this.setPrintImgUrls(addPrefix(res.result.pdfImage));
                this.pdfUrl = res.result.pdfUrl;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getReason () {
            const res = await getNoCrimeReasons();
            if (res && res.code === '0') {
                this.reasons = res.result.map((item) => {
                    return item.name;
                });
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        handleReason (useType) {
            this.useType = useType;
            this.changeStep('down');
        },
        async handlePrint () {
            // 打印证明
            const res = await toPrint({
                path: getPrintPrefix(this.pdfUrl)
            });
            if (res && res.code === 1) {
                await this.complete();
            } else {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }
        },
        async complete () {
            // 打印完成调用 办理完成结果
            const res = await proveComplete({});
            if (res && res.code === '0') {
                this.result = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async highPhotoNext () {
            const res = await uploadLetter({});
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        if (checkNoCrimeBusiness(this.CONSTANT.location)) {
            this.setSteps(NO_CRIME_BENGBU);
        } else {
            this.setSteps(NO_CRIME_YINCHUAN);
        }
    }
};

</script>
